export const useAuthStore = defineStore('auth', () => {
  // 
  const user = ref(null)
  const token = ref(null)
  const isLoggedIn = computed(() => !!user.value && !!token.value)
  const isAdmin = computed(() => user.value?.role === 'admin')
  const isLoading = ref(false)
  
  // Token storage keys
  const TOKEN_KEY = 'auth_token'
  const USER_KEY = 'auth_user'

  // 
  const login = async (credentials) => {
    try {
      isLoading.value = true
      
      //  -  'login'  'username'
      const loginData = {
        login: credentials.username,
        password: credentials.password
      }
      
      // API composable
      const { post } = useApi()
      const response = await post('/auth/login', loginData)
      
      if (!response.success) {
        throw new Error(response.error?.message || '')
      }
      
      const { user: userData, token: userToken } = response.data.data
      
      // token
      user.value = userData
      token.value = userToken
      
      //  localStorage
      if (process.client) {
        localStorage.setItem(TOKEN_KEY, userToken)
        localStorage.setItem(USER_KEY, JSON.stringify(userData))
      }
      
      return { success: true, user: userData, token: userToken }
    } catch (error) {
      console.error('Login error:', error)
      throw new Error(error.message || '')
    } finally {
      isLoading.value = false
    }
  }


  // 
  const logout = async (skipApiCall = false) => {
    try {
      // tokenAPI
      if (token.value && !skipApiCall) {
        const { post } = useApi()
        await post('/auth/logout')
      }
    } catch (error) {
      console.error('Logout API error:', error)
      // API
    } finally {
      // 
      user.value = null
      token.value = null
      
      //  localStorage
      if (process.client) {
        localStorage.removeItem(TOKEN_KEY)
        localStorage.removeItem(USER_KEY)
      }
      
      // 
      await navigateTo('/auth/login')
    }
  }

  // 
  const fetchUser = async () => {
    try {
      if (!token.value) return null
      
      const { get } = useApi()
      const response = await get('/auth/me')
      
      if (!response.success) {
        throw new Error('Failed to fetch user data')
      }
      
      const userData = response.data.data.user
      user.value = userData
      
      //  localStorage 
      if (process.client) {
        localStorage.setItem(USER_KEY, JSON.stringify(userData))
      }
      
      return userData
    } catch (error) {
      console.error('Fetch user error:', error)
      // 
      await logout(true)
      return null
    }
  }
  
  //  (SSR)
  const initializeAuth = async () => {
    if (process.client) {
      const savedToken = localStorage.getItem(TOKEN_KEY)
      const savedUser = localStorage.getItem(USER_KEY)
      
      if (savedToken && savedUser) {
        try {
          token.value = savedToken
          user.value = JSON.parse(savedUser)
          
          // token
          // tokenmiddleware
        } catch (error) {
          console.error('Failed to parse saved auth data:', error)
          // 
          localStorage.removeItem(TOKEN_KEY)
          localStorage.removeItem(USER_KEY)
          user.value = null
          token.value = null
        }
      }
    }
  }

  // 
  const updateProfile = async (profileData) => {
    try {
      const { put } = useApi()
      const response = await put('/profile', profileData)
      
      if (!response.success) {
        throw new Error(response.error?.message || '')
      }
      
      const updatedUser = response.data.data.user
      user.value = updatedUser
      
      //  localStorage
      if (process.client) {
        localStorage.setItem(USER_KEY, JSON.stringify(updatedUser))
      }
      
      return { success: true, user: updatedUser }
    } catch (error) {
      console.error('Update profile error:', error)
      throw new Error(error.message || '')
    }
  }
  
  // 
  const changePassword = async (passwordData) => {
    try {
      const { put } = useApi()
      const response = await put('/auth/change-password', passwordData)
      
      if (!response.success) {
        throw new Error(response.error?.message || '')
      }
      
      return { success: true, message: '' }
    } catch (error) {
      console.error('Change password error:', error)
      throw new Error(error.message || '')
    }
  }
  
  // token
  const refreshToken = async () => {
    try {
      const { post } = useApi()
      const response = await post('/auth/refresh')
      
      if (!response.success) {
        throw new Error('Token refresh failed')
      }
      
      const newToken = response.data.data.token
      token.value = newToken
      
      //  localStorage
      if (process.client) {
        localStorage.setItem(TOKEN_KEY, newToken)
      }
      
      return newToken
    } catch (error) {
      console.error('Refresh token error:', error)
      await logout(true)
      throw error
    }
  }

  return {
    // 
    user: readonly(user),
    token: readonly(token),
    isLoggedIn,
    isAdmin,
    isLoading: readonly(isLoading),
    
    // 
    login,
    logout,
    initializeAuth,
    fetchUser,
    
    // 
    updateProfile,
    changePassword,
    refreshToken
  }
})